import axios from 'axios'
import * as config from '../utils/config'
import Cryptojs from 'crypto-js'

let base = config.baseApi;

export function getHeader(method, url, content_type='application/json') {
    let timestamp = new Date().getTime();
    let headers = {
        "Content-Type": content_type,
        // "Nonce": Math.floor(Math.random()*(1e9-1e7+1)+1e7),
        // "SecretId": config.SecretId,
        "Nonce": 123456,
        "SecretId": "ZDNUI3BF",
        "Signature": "",
        "SignatureMethod": "HmacSHA256",
        // "Timestamp": timestamp
        "Timestamp": 1587646708116
    };
    let source = method + url + "?Nonce=" + headers["Nonce"] + "&SecretId=" + headers["SecretId"];
    source += "&SignatureMethod=" + headers["SignatureMethod"] + "&Timestamp=" + timestamp;
    // headers["Signature"] = Cryptojs.HmacSHA512(source, config.key).toString();
    headers["Signature"] = Cryptojs.HmacSHA512(source, "password").toString();
    return headers;
}

export const postRequest = (url, params) => {
    return axios({
        method: 'post',
        url: `${base}${url}`,
        data: params
    })
};
export const putRequest = (url, params) => {
    return axios({
        method: 'put',
        url: `${base}${url}`,
        data: params
    })
};
export const getRequest = (url, params) => {
    let apiUrl = `${base}${url}`;
    apiUrl = getApiUrl(apiUrl,params);
    return axios({
        method: 'get',
        url: apiUrl,
        data: params
    })
};
export const deleteRequest = (url, params) => {
    let apiUrl = `${base}${url}`;
    apiUrl = getApiUrl(apiUrl,params);
    return axios({
        method: 'delete',
        url: apiUrl,
        data: params
    })
};

function getApiUrl(url,params){
    let apiUrl = url;
    let i = 0;
    for (const key in params) {
        if (params.key !== undefined) {
            const value = params.key;
            const param = key+"="+value;
            let s = '?';
            if ( i > 0 ){
                s = '&';
            }
            apiUrl = apiUrl+s+param;
            i++;
        }
    }
    return apiUrl;
}